import eventBus from '@/assets/js/eventBus.js'

export const defaultAliasesV2 = {
  amazon: (args) => { return redirect('https://www.amazon.com', '/s/?field-keywords=', args) },
  g: (args) => { return redirect('https://www.google.com/', '/s/?field-keywords=', args) },
  dg: (args) => { return redirect('https://duckduckgo.com/', '?q=', args) },
  r: (args) => {
    switch (args.length) {
      case 1:
        return redirect('https://reddit.com', undefined, args);
      case 2:
        return redirect(`https://reddit.com/r/${args[1]}`, undefined, [args[0]]);
      case 3:
        return redirect(`https://reddit.com/${(args[1] !== 's') ? `r/${args[1]}/` : ''}`, 'search?q=', args.slice(1)) + `${(args[1] === 's') ? '' : '&restrict_sr=on'}`;
      default:
        return redirect(`https://reddit.com/${(args[1] !== 's') ? `r/${args[1]}/` : ''}`, 'search?q=', args.slice(1)) + `&sort=${args[3]}${(args[1] === 's') ? '' : '&restrict_sr=on'}`;
    }
  },
  // TODO Handle settings
  set: {
    subpanel: () => { eventBus.$emit('sub_panel') },
    addpanel: () => { eventBus.$emit('add_panel') }
  }
  // TODO add new integrations
}

function redirect(url, searchKey, args, type) {
  console.log(`args length: ${args.length}`);
  switch (args.length) {
    case 1:
      console.log(`return url ${url}`);
      return url;
    default:
      console.log(`return search url`);
      return `${url}${searchKey}${(type) ? args[1].replace(/ /g, '+') : encodeURIComponent(args[1])}`;
  }
}



