<template>
<div class="container">
  <div class="cli-container">
    <form v-on:submit="runCliV2($event)">
      <span>></span><input v-model="command" type="text" />
    </form>
  </div>
  <span class="time">{{ computedTime }}</span>
</div>
</template>

<script>
import manager from '@/assets/js/storageManager.js'
import eventBus from '@/assets/js/eventBus.js'
import { defaultAliases } from '@/assets/js/aliases.js'
import { defaultAliasesV2 } from '@/assets/js/aliasesV2.js'

function checkUrl(url, newtab) {
  console.log(url.slice(0, 7));
  if (url.slice(0, 7) !== 'http://' && url.slice(0, 8) !== 'https://') {
    url = `https://${url}`;
  }
  console.log(url);
  if (/(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_+.~#?&//=]*)/.test(url)) {
    if (!url.includes(' ')) {
      if (newtab) {
        window.open(url).focus();
        return true;
      } else {
        window.location.href = url;
        return true;
      }
    } else {
      return false;
    }
  }
}

export default {
  name: 'CommandLine',
  props: {},
  data() {
    return {
      command: '',
      time: new Date(),
      defaultAliases,
      customAliases: (manager.getCliConfig().aliases) ? manager.getCliConfig().aliases : false,
      config: manager.getCliConfig()
    }
  },
  created() {
    this.attachToEventBus();

    const self = this;
    setInterval(function() {
      self.time = new Date()
    }, 1000)
  },
  computed: {
    computedTime() {
      return `${this.time.getHours() % 12}:${(String(this.time.getMinutes()).length === 2) ? this.time.getMinutes() : '0' + this.time.getMinutes()}${(this.time.getMinutes() > 12) ? 'pm' : 'am'}`
    },
    computedDefaultCommand() {
      for (var i = 0; i < this.defaultAliases.length; i++) {
        const alias = this.defaultAliases[i];
        if (alias.alias === this.config.defaultCommand) {
          return alias;
        }
      };
    }
  },
  methods: {
    attachToEventBus() {
      eventBus.$on('commit', () => {
        // attaching to manager
      })
    },
    runCliV2(e) {
      e.preventDefault();
      const args = this.command.split(';');
      // Check if last arg is new tab flag
      const newTab = (args[args.length - 1] === 'n');
      // Remove new tab arg
      if (args.length > 1 && args[args.length - 1] === 'n') {
        console.log(`removing new tab key`)
        args.pop();
      }
      console.log(args);
      // Get array of all defaultAliases
      console.log(defaultAliasesV2);
      const aliases = Object.keys(defaultAliasesV2);
      // Check if first alias is in aliases
      if (aliases.includes(args[0])) {
        // TODO handle 'set' arguments to enable settings
        // Open in new tab if newTab === true
        if (newTab) {
          window.open(defaultAliasesV2[args[0]](args));
        } else {
          window.location.href = defaultAliasesV2[args[0]](args);
        }
      } else {
        // TODO navigate if url or search with default command
        // TODO default command
      }
    },
    runCli(e) {
      e.preventDefault();
      const args = this.command.split(';');
      const validUrl = checkUrl(args[0], (args[1] === 'n'));
      console.log(validUrl);
      if (!validUrl) {
        switch (args[0]) {
          case 'subpanel':
            eventBus.$emit('sub_panel');
            break;
          case 'addpanel':
            eventBus.$emit('add_panel');
            break;
          case 'save':
            // Tells all rendered components to attach to storage manager
            eventBus.$emit('commit');
            manager.loadToStorage();
            console.log('Saved to local storage');
            break;
          case 'set':
            switch (args[1]) {
              case 'defaultCommand':
                if (args[2]) {
                  this.config.defaultCommand = args[2];
                  manager.attachCliConfig(this.config);
                  manager.loadToStorage();
                  manager.loadFromStorage();
                  this.command = '';
                }
                break;
              default:
                break;
            }
            break;
          default:
            var navigated = false
            if (args[0].includes(' ')) {
              checkUrl(`${this.computedDefaultCommand.url}${this.computedDefaultCommand.searchPattern}${args[0].replace(/ /g, this.computedDefaultCommand.space)}`, args[1] === 'n');
            } else {
              console.log(this);
              for (var i = 0; i < this.defaultAliases.length; i++) {
                const alias = this.defaultAliases[i];
                if (alias.alias === args[0]) {
                  console.log(`alias found ${alias.alias}`);
                  if (args[1]) {
                    if (args[1] !== 'n') {
                      navigated = true;
                      checkUrl(`${alias.url}${alias.searchPattern}${args[1].replace(/ /g, alias.space)}`, args[2] === 'n');
                      break;
                    } else {
                      navigated = true;
                      checkUrl(alias.url, true);
                      break;
                    }
                  } else {
                    console.log(`no arg 2 navigating to ${alias.url}`);
                    navigated = true;
                    checkUrl(alias.url, false);
                    break;
                  }
                }
              };
              if (this.customAliases) {
                // this.customAliases.forEach((alias) =>
                for (var j = 0; j < this.customAliases.length; j++) {
                  const alias = this.customAliases[j];
                  if (alias.alias === args[0]) {
                    if (args[1]) {
                      if (args[1] !== 'n') {
                        navigated = true;
                        checkUrl(`${alias.url}${alias.searchPattern}${args[1].replace(/ /g, alias.space)}`, args[2] === 'n');
                        break;
                      } else {
                        navigated = true;
                        checkUrl(alias.url, true);
                        break;
                      }
                    } else {
                      navigated = true;
                      checkUrl(alias.url, false);
                      break;
                    }
                  }
                };
              }
              if (!navigated) {
                checkUrl(`${this.computedDefaultCommand.url}${this.computedDefaultCommand.searchPattern}${args[0].replace(/ /g, this.computedDefaultCommand.space)}`, args[1] === 'n');
              }
            } // window.location.replace(args[0]);
        }
      }
    }
  }
}
</script>

<style>
* {
  font-size: 35px;
  font-family: 'Roboto Mono', monospace;
  color: #535964;
;
}

div.container {
  margin-top: 15px;
  /* display: flex; */
  position: fixed;
  bottom: 0;
  width: 100%;
}

span.time {
  position: fixed;
  right: 5px;
  font-size: 25px;
  bottom: 0px;
}

div.cli-container {
  float: left;
  width: 80%;
}

input {
  font-size: 18px;
  border: none;
  width: 80%;
  float: left;
  background-color: #212328;
  color: #646973;
}

div.cli-container span {
  float: left;
}

span {
  font-size: 18px;
  padding: 3px 5px 0.6px 0;
  margin-left: 5px;
}
</style>



